import { Link } from "react-router-dom"

const FileDetail = ({
    label,
    fileName,
    btnLabel,
    url,
    target,
    ...props
}: FileDetailT) => {
    return (
        <div className="row align-items-center mb-5">
            <p className="mb-0">{label}</p>
            <p className="col my-auto text-success">{fileName}</p>
            <Link
                className="col-md-4 btn btn-success btn-lg fs-6"
                to={url}
                rel="noreferrer"
                target={target}
                {...props}
            >
                {btnLabel}
            </Link>
        </div>
    )
}

export default FileDetail
