import { useCallback, useEffect, useState } from "react"
import Card from "../../Components/Card"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageContent from "../Components/PageContent"
import PageTitle from "../Components/PageTitle"
import { Link, useParams } from "react-router-dom"
import { get, patch, put } from "../../Functions/Api"
import FileDetail from "../../Components/FileDetail"
import FormGroup from "../../Components/FormGroup"
import Button from "../Components/Buttons/Button"
import { PopupAlert, UploadFileAlert } from "../../Functions/Alerts"

const DetailSurat = () => {
    const [detailSurat, setDetailSurat] = useState<DataT>()
    const { type, id, jenis_surat } = useParams()

    const getDetailSurat = useCallback(async () => {
        const res = await get(`surat/${jenis_surat}/${id}`)
        if (res.ok) setDetailSurat(res.data)
        console.log(res.data)
        return
    }, [jenis_surat, id])

    useEffect(() => {
        getDetailSurat()
    }, [getDetailSurat])

    const apply: React.MouseEventHandler<HTMLButtonElement> = (evt) => {
        const id = evt.currentTarget.dataset?.nik

        UploadFileAlert(async (form) => {
            const res = await put(`surat/upload/${id}`, form)
            const ayam = await patch(`surat/${id}/acc`)
            console.log(ayam)
            if (res.ok) {
                getDetailSurat()
                PopupAlert.fire({
                    icon: "success",
                    text: res.data?.message
                })
            }
        })
    }

    console.log(jenis_surat)

    return (
        <>
            <PageTitle title="Data Surat" />
            <ButtonBack />
            <PageContent>
                <Card
                    cardHeader={
                        <h4 className="card-title text-success">
                            Detail Pengajuan Surat
                        </h4>
                    }
                    cardClassName="p-3 mt-3"
                    cardBody={
                        <form>
                            <div className="row">
                                <div className="col-md-6">
                                    {detailSurat?.surat_pengantar && (
                                        <FileDetail
                                            label="Surat Pengantar"
                                            fileName="Surat Pengantar.pdf"
                                            btnLabel="Lihat Surat"
                                            url={`/${type}/data-surat/${jenis_surat}/${id}/surat-pengantar?nama_file=${detailSurat.surat_pengantar}`}
                                            target="_blank"
                                        />
                                    )}
                                    <FileDetail
                                        label="Surat yang Diajukan"
                                        fileName={`Surat ${jenis_surat}`}
                                        btnLabel="Edit Surat"
                                        url={`/${type}/data-surat/${jenis_surat}/${id}/edit?nama=${detailSurat?.surat.info.nama}`}
                                    />
                                </div>
                                <div className="col-md-6">
                                    <FormGroup
                                        label="NIK Pengaju Surat"
                                        id="nik"
                                        name="nik"
                                        defaultValue={
                                            detailSurat?.surat.pemohon.nik
                                        }
                                        disabled
                                    />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12 d-flex justify-content-end gap-3">
                                    <Button
                                        text="Setujui"
                                        className="btn-primary"
                                        onClick={apply}
                                        type="button"
                                        disabled={detailSurat?.surat.acc}
                                    />

                                    <Link
                                        to={`/${type}/data-surat/${jenis_surat}/${id}/edit?nama=${detailSurat?.surat.info.nama}`}
                                        className="btn btn-info btn-lg fs-6"
                                    >
                                        Cetak
                                    </Link>
                                </div>
                            </div>
                        </form>
                    }
                />
            </PageContent>
        </>
    )
}

export default DetailSurat
