import { Link } from "react-router-dom"
import { memo } from "react"
import { useAuth } from "../../Hooks/useAuth"

const PageTitle = ({ title }: PageTitleT) => {
    const { role } = useAuth()

    return (
        <>
            <header className="mb-3">
                <Link to="#" className="burger-btn d-block d-xl-none">
                    <i className="bi bi-justify fs-3"></i>
                </Link>
            </header>
            <div className="page-heading d-flex justify-content-between">
                <div className="page-title">
                    <h3>{title}</h3>
                </div>
                <div className="user-avatar d-flex">
                    <div
                        className="avatar bg-secondary me-2"
                        style={{ height: "32px" }}
                    >
                        <span className="avatar-content">
                            <i className="bi bi-person text-primary"></i>
                        </span>
                    </div>
                    <Link to={`/${role}/ubah-profil`}>
                        <p className="avatar-detail pt-1">
                            {role === "user" ? "User" : "Admin"} JK
                        </p>
                    </Link>
                </div>
            </div>
        </>
    )
}

export default memo(PageTitle)
